import Vue from 'vue'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'

import moment from 'moment'

// 承载
import testProductDetail from '../pages/testProductDetail.vue'
// 主页
import testProductDetailIndex from '../views/test-product-detail.vue'

Vue.use(Router)

Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});

const router = new Router({
  routes: [{
    path: '/',
    name: 'testProductDetail',
    component: testProductDetailIndex
  }]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#testProductDetail',
  router,
  components: {
    testProductDetail
  },
  template: '<testProductDetail/>'
})



